<template>
    <div class="page">
        <promo :data="promoStr"></promo>
        <div class="card">
            <div class="status-list">
                <div class="pull-refresh-container">
                    <div class="status-editor-bar">
                        <div class="avatar">
                            <img src="https://img1.doubanio.com/icon/user_normal.jpg" alt="æœªç™»å½•">
                        </div>
                        <div class="holder">ç™»å½•å‘å¹¿æ’­</div>
                        <div class="icon icon-camera"></div>
                        <div class="icon icon-pen"></div>
                    </div>
                    <div class="pull-refresh-loading"></div>
                    <ul class="status-list" v-show="show">
                        <broad-item v-for="item in getBroad.items" :key="item.id" :data="item"></broad-item>
                        <a href="" class="list-link">æ˜¾ç¤ºæ›´å¤šå¹¿æ’­</a>
                    </ul>
                </div>
            </div>
        </div>
        <download-app></download-app>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import promo from './promo'
import broadItem from './broadItem'
import downloadApp from './downloadApp'
export default {
    name: 'broadcast',
    components: {
        promo,
        broadItem,
        downloadApp
    },
    data(){
        return {
            promoStr: ['æ‰“å¼€App, å›žå¤å¹¿æ’­'],
            show: false
        }
    },
    methods: {
        ...mapActions([
            'postBroad',
            'getBroadData'
        ])
    },
    computed: {
        ...mapGetters([
            'getBroad'
        ])
    },
    mounted() {
        
        if(!this.getBroad.show){
            this.postBroad()
          
            this.$watch("getBroad.show", function(){
                this.show = true
                console.log(this.getBroad)
            }, {deep:true})
        }else{
            this.show = true
        }
    }
  
}
</script>

<style scoped>
    .card {
        margin: 0 36px;
    }
    .pull-refresh-container {
        position: relative;
    }
    .status-editor-bar {
        position: relative;
        margin: 0 -36px 10px;
        padding: 20px 36px;
        cursor: pointer;
        overflow: hidden;
    }
    .status-editor-bar .avatar {
        width: 80px;
        height: 80px;
        float: left;

    }
    .status-editor-bar .avatar img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
    }
    .status-editor-bar .holder {
        color: #aaa;
        padding-left: 20px;
        font-size: 30px;
        line-height: 80px;
        float: left;
    }
    .status-editor-bar .icon {
        width: 80px;
        height: 80px;
        padding: 16px;
        margin-right: 20px;
        position: relative;
        float: right;
        box-sizing: border-box;
    }
    .status-editor-bar .icon.icon-camera {
        margin-right: 0;
    }
    .status-editor-bar .icon::after {
        content: '';
        position: absolute;
        width:48px;
        height: 48px;
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
    }
    .status-editor-bar .icon.icon-camera::after {
        background-image: url(https://img3.doubanio.com/f/talion/68ab505eac3e7551815c06a5e03a26a6373bb46c/pics/card/ic_status_camera.svg);
    }
    .status-editor-bar .icon.icon-pen::after {
        background-image: url(https://img3.doubanio.com/f/talion/d9ba3fa414721588284167944c70664e685993b1/pics/card/ic_status_pen.svg);
    }
    .status-editor-bar::after {
        left: 0;
        bottom: 0;
        width: 100%;
        height: 2px;
        background: #e8e8e8;
        content: '';
        position: absolute;
        transform: scaleY(0.5);
        -webkit-transform: scaleY(0.5);
    }
    .list-link {
        display: block;
        padding: 30px 0;
        font-size: 32px;
        line-height: 36px;
        text-align: center;
        cursor: pointer;
    }
</style>



